<template>
  <div class="down-area">
    <img src="../assets/img/down-load.png" alt width="100%" height="100%" />
    <div class="down-block"></div>
    <div class="down-btn-area">
      <button class="down-btn">
        <span>APP STORE</span>
        <div class="down-icon">
          <img src="../assets/img/apple-icon-black.png" alt width="100%" height="100%" />
        </div>
      </button>
      <button class="down-btn">
        <span>PLAY STORE</span>
        <div class="down-icon">
          <img src="../assets/img/play-icon-black.png" alt width="100%" height="100%" />
        </div>
      </button>
    </div>
    <!-- <note-foot class="bg-gray"></note-foot> -->
  </div>
</template>
<script>
import NoteFoot from "@/components/NoteFoot.vue";
export default {
  components: {
    "note-foot": NoteFoot
  }
};
</script>
<style scoped>
.bg-gray {
  background-color: #dcdcdc;
}
button {
  margin: 0;
  padding: 0;
  border: 1px solid transparent;
  background-color: transparent;
  outline: none;
}
.down-area {
  position: relative;
  width: 100%;
  height: 91.85vh;
  /* background-color: #dcdcdc; */
}
.down-area .down-block {
  position: absolute;
  top: 28.52vh;
  left: 22.5vw;
  width: 11.04vw;
  height: 19.63vh;
  background-color: #979797;
  border: 10px solid #f5f5f5;
  border-radius: 2px;
}
.down-area .down-btn-area {
  position: absolute;
  top: 56.11vh;
  left: 13.75vw;
  display: flex;
  justify-content: space-between;
  width: 592px;
  height: 76px;
  font-size: 28px;
  font-family: SimHei;
  font-weight: bold;
  color: #666666;
  line-height: 76px;
}
.down-area .down-btn-area .down-btn {
  position: relative;
  width: 266px;
  height: 76px;
  cursor: pointer;
  padding-left: 104px;
  background: #ffffff;
  border: 4px solid #ffffff;
  text-align: left;
  opacity: 0.8;
  border-radius: 6px;
}
.down-area .down-btn-area .down-btn .down-icon {
  position: absolute;
  top: 4px;
  left: 20px;
  width: 60px;
  height: 60px;
}
</style>


