    color: #999999;
  }
  .new-area .new-info .info-date .date-short-line {
    width: 30px;
    height: 6px;
    margin-top: 20px;
    background-color: #70d6d1;
    border-radius: 2px;
  }
  .new-area .new-info .info-date .date-line {
    position: absolute;
    top: 10px;
    left: 90px;
    width: 17px;
    height: 33px;
  }
  /* 行的间距有问题 */
  .new-area .info-detail {
    width: 285px;
    height: 88px;
    padding-top: 30px;
    font-size: 14px;
    font-family: MicrosoftYaHei;
    font-weight: 400;
    color: #333333;
    overflow: hidden;
  }
  .new-area .info-detail-height {
    line-height: 28px;
  }
  .new-area .view-info {
    position: relative;
    width: 134px;
    height: 48px;
    margin-top: 40px;
    padding-left: 19px;
    cursor: pointer;
    font-size: 14px;
    font-family: MicrosoftYaHei;
    font-weight: bold;
    color: #70d6d1;
    line-height: 40px;
    border: 2px solid #70d6d1;
    border-radius: 4px;
    text-align: left;
    transition: all 0.2s;
  }
  .new-area .view-info:hover {
    transform: scale(1.1);
  }
  .new-area .view-info .arrow-icon {
    position: absolute;
    top: 17px;
    left: 95px;
    width: 18px;
    height: 7px;
  }

  .new-area .to-info {
    position: relative;
    width: 76px;
    height: 42px;
    margin-top: 40px;
    cursor: pointer;
    background: #70d6d1;
    border-radius: 4px;
    transition: all 0.2s;
  }

  .new-area .to-info:hover {
    transform: scale(1.1);
  }

  .new-area .to-info .arrow-icon-w {
    position: absolute;
    top: 16px;
    left: 30px;
    width: 18px;
    height: 7px;
  }

  .map-area {
    position: relative;
    width: 100%;
    height: 375px;
  }

  .map-area .map-msg {
    position: absolute;
    top: 0;
    right: 0;
    width: 28%;
    height: 255px;
    padding: 60px 5%;
    color: #ffffff;
    background-color: #16b9b1;
    z-index: 1;
  }

  .map-area .map-msg .map-line {
    margin-top: 20px;
    width: 18px;
    height: 4px;
    background: #ffffff;
    opacity: 0.6;
    border-radius: 2px;
  }

  .map-area .map-msg .map-title {
    font-size: 28px;
    font-family: Adobe Heiti Std R;
    font-weight: normal;
    color: #ffffff;
  }

  .map-area .map-msg .map-title-en {
    margin-top: 10px;
    font-size: 16px;
    font-family: MyriadPro-Regular;
    font-weight: 400;
    color: #ffffff;
    opacity: 0.6;
  }
  .map-area .map-msg .map-info-h {
    margin-top: 24px;
    font-size: 16px;
    font-family: SimHei;
    font-weight: 400;
    color: #ffffff;
  }
  .map-area .map-msg .map-info {
    margin-top: 12px;
    font-size: 16px;
    font-family: SimHei;
    font-weight: 400;
    color: #ffffff;
  }
}
</style>



