<template>
  <div>
    <div class="product-detail">
      <div class="p-line-left"></div>
      <div class="p-contain">
        <div class="p-contain-info">{{$t('sale.afterSale')}}</div>
      </div>
      <div class="p-info">after sale</div>
      <div class="p-line-right"></div>
    </div>
    <div class="sale-info-area">
      <div class="btn-area">
        <button class="btn-f">常见问题</button>
        <button class="btn">售后服务</button>
        <button class="btn">售后政策</button>
        <button class="btn">联系我们</button>
      </div>
      <div class="m-sale-title">常见问题</div>
      <div class="sale-info">
        <div v-for="(infos,index) in informations" :key="infos.id">
          <div>{{ index + 1 }}、{{ infos.title }}</div>
          <div>{{ infos.contain }}</div>
        </div>
      </div>
    </div>
    <note-foot class="bg-gray"></note-foot>
  </div>
</template>

<script>
import NoteFoot from "@/components/NoteFoot.vue";
export default {
  components: {
    "note-foot": NoteFoot
  },
  data() {
    return {
      informations: [
        {
          id: 0,
          title: "产品如何使用？",
          contain:
            "可以在各大安卓应用市场和APP Store中搜索并下载“喵喵机”APP，注册并登陆后通过，点击添加设备，搜索并连接设备，随后便可在APP里操作打印。"
        },
        {
          id: 1,
          title: "需要加墨吗？",
          contain:
            "此款打印机采用热敏打印技术，不需要墨、色带或者碳带等耗材，只需放入官方打印纸便可轻松打印。"
        },
        {
          id: 2,
          title: "能打印彩色图片吗？",
          contain:
            "可以在各大安卓应用市场和APP 目前喵喵机P1和P2仅支持单色打印，不可多彩色打印。"
        },
        {
          id: 3,
          title: "打印机如何换纸？",
          contain:
            "查看喵喵机左侧的箭头指示，按照指示轻轻推开喵喵机就可以打开喵喵机，然后进行热敏纸更换或者调整。"
        },
        {
          id: 4,
          title: "支持什么耗材？",
          contain:
            "此款机器支持普通热敏纸，标签热敏纸和纸张背面整面带胶的不干胶热敏纸。官方热敏纸耗材不含双酚A且均通过双酚A检测。"
        },
        {
          id: 5,
          title: "打印出来的图片或者文档可以留影多久？",
          contain:
            "根据所选用官方耗材的留影时长不同而不同，留影时长为1-10年不等。"
        },
        {
          id: 6,
          title: "打印机有多大？",
          contain: "只有一个成年人单手可以握起的巴掌大小。"
        },
        {
          id: 7,
          title: "可以通过PC打印吗？",
          contain:
            "我们特地开发了PC客户端，通过USB线连接到电脑即可便捷打印，客户端可以到 喵宝科技官网下载安装。"
        },
        {
          id: 8,
          title: "指示灯小贴士",
          contain:
            "指示等常亮绿色或者白色为正常工作状态。红灯常亮，为异常工作状态，异常情况有缺纸、过热、开盖、低电量。"
        },
        {
          id: 9,
          title: "关于充电",
          contain:
            "本产品适用任何标准Micro-USB充电头充电亦可接电脑充电。喵喵机P1充满一次电在正常情况下可打印4卷纸，喵喵机P2可打印6卷纸。"
        }
      ]
    };
  }
};
</script>
<style scoped>
@media screen and (max-width: 1200px) {
  .product-detail {
    display: none;
  }
  .sale-info-area .btn-area {
    display: none;
  }

  .sale-info-area {
    background-color: #f6f6f6;
  }
  .sale-info-area .m-sale-title {
    height: 64px;
    padding-top: 40px;
    font-size: 34px;
    font-family: SimHei;
    font-weight: bold;
    color: #333333;
    text-align: center;
    line-height: 64px;
  }
  .sale-info-area .sale-info {
    padding: 20px;
    font-size: 28px;
    font-family: SimHei;
    font-weight: 400;
    color: #666666;
    line-height: 56px;
  }
}

@media screen and (min-width: 1199px) {
  .bg-gray {
    background-color: #dcdcdc;
  }
  .sale-info-area .m-sale-title {
    display: none;
  }
  button {
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
    background-color: transparent;
    outline: none;
  }
  .product-detail {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 162px;
    font-size: 28px;
    font-family: SimHei;
    color: #222222;
  }

  .product-detail .p-contain {
    flex: 2;
    display: flex;
    align-items: flex-end;
  }

  .product-detail .p-contain-info {
    margin: 0 auto;
    margin-bottom: 10px;
  }

  .product-detail .p-line-left {
    position: absolute;
    top: 80px;
    left: 40%;
    width: 112px;
    height: 1px;
    background-color: #d2d2d2;
  }
  .product-detail .p-line-right {
    position: absolute;
    top: 80px;
    right: 40%;
    width: 112px;
    height: 1px;
    background-color: #d2d2d2;
  }
  .product-detail .p-info {
    flex: 1;
    font-size: 16px;
    color: #777777;
    font-family: MyriadPro-Regular;
    font-weight: 400;
    text-align: center;
    /* line-height: 20px; */
  }
  .sale-info-area {
    display: flex;
    margin-top: 20px;
    margin-bottom: 100px;
  }
  .sale-info-area .btn-area {
    width: 24.69vw;
    height: 75.68vh;
  }
  .sale-info-area .btn-area .btn-f {
    width: 164px;
    height: 42px;
    cursor: pointer;
    margin-left: 12.19vw;
    border: 1px solid #a0a0a0;
    border-radius: 4px;
  }
  .sale-info-area .btn-area .btn {
    width: 164px;
    height: 42px;
    cursor: pointer;
    margin-top: 60px;
    margin-left: 12.19vw;
    color: #ffffff;
    background-color: #515151;
    border-radius: 4px;
  }
  .sale-info-area .btn-area .btn:hover {
    color: #515151;
    background-color: #ffffff;
    border: 1px solid #a0a0a0;
  }
  .sale-info-area .sale-info {
    width: 63.96vw;
    height: 72.04vh;
    font-size: 18px;
    font-family: SimHei;
    font-weight: 400;
    color: #000000;
    line-height: 40px;
  }
}
</style>


